import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { ProgramConfig } from "../Model/ProgramConfigModel";

const initialState: ProgramConfig[] = []

const programConfigSlice = createSlice({
  name: 'programConfig',
  initialState,
  reducers: {
    setProgramConfig: (state, action: PayloadAction<ProgramConfig[]>) => {
      console.log("SetProgramConfig", action.payload)
      return action.payload
    },
    addProgramConfig: (state, action: PayloadAction<ProgramConfig>) => {
      console.log("AddProgramConfig", action.payload)
      state.push(action.payload)
    },
    deleteProgramConfig: (state, action: PayloadAction<number>) => {
      return state.filter((item) => item.id !== null && item.id !== action.payload);
    },
    updateProgramConfig: (state, action) => {
      const index = state.findIndex(ProgramConfig => ProgramConfig.id === action.payload.id);
      if (index !== -1) {
        state[index] = action.payload;
      }
    }
  }

})

export const { addProgramConfig, deleteProgramConfig, updateProgramConfig, setProgramConfig } = programConfigSlice.actions
export default programConfigSlice.reducer