
import { programConfigApi } from "../services/ProgramService";
import { authApi } from "../services/AuthService";
import programConfigSlice from "./ProgramConfigSlice";
import authSlice from "./LoginSlice";
import { configureStore } from "@reduxjs/toolkit";

export const store = configureStore({

  reducer: {
    programConfig: programConfigSlice,
    auth: authSlice,
    [programConfigApi.reducerPath]: programConfigApi.reducer,
    [authApi.reducerPath]: authApi.reducer,
  },
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware().concat(
      authApi.middleware,
      programConfigApi.middleware,
    )
});

// Infer the type of `store`
export type AppStore = typeof store
// Infer the `AppDispatch` type from the store itself
export type AppDispatch = typeof store.dispatch
// Same for the `RootState` type
export type RootState = ReturnType<typeof store.getState>

export default store;
