import { createApi, fetchBaseQuery, FetchBaseQueryError } from "@reduxjs/toolkit/query/react";
import { ProgramConfig } from "../Model/ProgramConfigModel";
import { toast } from "react-toastify";
import { handleQueryError } from "../Utils/ErrorHandler";
import { ENDPOINT } from "../appConfig";
import { ProgramConfigRequestDTO } from "../Model/DTO/ProgramConfigRequestDTO";
import { RootState } from "../context/Store";

// Criando a API Standalone (sem Redux Store)
export const programConfigApi = createApi({
  reducerPath: "programConfigApi",
  baseQuery: fetchBaseQuery({ baseUrl: ENDPOINT+"/ProgramConfig",
    prepareHeaders: (headers, { getState }) => {
          const token = (getState() as RootState).auth.token;
          if (token) {
            headers.set('Authorization', `Bearer ${token}`);
          }
          return headers;
        },
  }),
  endpoints: (builder) => ({
    getProgramConfigsApi: builder.query<ProgramConfig[], void>({
      query: () => "",
    }),
    addProgramConfigApi: builder.mutation< ProgramConfig, ProgramConfigRequestDTO>({
      query: (programConfig) => ({
        url: "",
        method: "POST",
        body: programConfig,
      }),
      onQueryStarted: async (program, { queryFulfilled }) => {
        try {
           const {data} = await queryFulfilled;
           console.log(data)
        } catch (error) {
          toast.error(handleQueryError(error as FetchBaseQueryError))
        }
      },
    }),
    updateProgramConfigApi: builder.mutation<void, ProgramConfig>({
      query: ( programConfig ) => ({
        url: `/${programConfig.id}`,
        method: "PUT",
        body: programConfig,
      }),
      onQueryStarted: async (program, { queryFulfilled }) => {
        try {
           await queryFulfilled;
        } catch (error) {
          console.log(error)
         toast.error(handleQueryError(error as FetchBaseQueryError))
        }
      },
    }),
    deleteProgramConfigApi: builder.mutation<void, number>({
      query: (id) => ({
        url: `/${id}`,
        method: "DELETE",
      }),
      onQueryStarted: async (program, { queryFulfilled }) => {
        try {
           await queryFulfilled;
        } catch (error) {
          console.log(error)
          toast.error(handleQueryError(error as FetchBaseQueryError))
        }
      },
    }),
  }),
});

// Exportando os hooks automaticamente gerados
export const {
  useGetProgramConfigsApiQuery,
  useAddProgramConfigApiMutation,
  useUpdateProgramConfigApiMutation,
  useDeleteProgramConfigApiMutation,
} = programConfigApi;
