import { yupResolver } from "@hookform/resolvers/yup";
import { Box, Button, Grid2, Modal, SxProps, TextField, Theme, Typography } from '@mui/material';
import { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from "react-redux";
import * as Yup from "yup";
import { AppDispatch, RootState } from "../context/Store";
import { useLoginMutation, useSigninMutation } from "../services/AuthService";
import { toast } from "react-toastify";
import { setCredentials } from "../context/LoginSlice";
import { AuthRequest } from "../Model/DTO/AuthRequest";
type Props = {
  isLogIn: boolean
  open: boolean,
  handleClose: VoidFunction
}

const style: SxProps<Theme> = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  borderRadius: 3,
  boxShadow: 24,
  p: 4,
  display: "flex",
  flexDirection: "column",
  gap: 1
};

const validation = Yup.object().shape({
  username: Yup.string().required("Precisa de um username"),
  password: Yup.string().required("Precisa de uma senha").min(5, "Muito curta"),
});

const ModalLogin = ({ open, handleClose, isLogIn }: Props) => {
  const auth = useSelector((state: RootState) => state.auth)
  const [LoginApi] = useLoginMutation();
  const [SigninApi] = useSigninMutation();
  const dispatch = useDispatch<AppDispatch>();
  const { register, handleSubmit, formState: { errors }, reset, } = useForm<AuthRequest>({ resolver: yupResolver(validation) });

  useEffect(() => {
    if (open) {
      reset();
    }
  }, [open]);

  async function handleLogin(form: AuthRequest) {
    await LoginApi(form)
    .unwrap()
    .then((res)=>{
      console.log(res)
      dispatch(setCredentials(res))
      toast.success("Logado com sucesso")  
      handleClose();    
    }).catch(()=>{})
  }

  async function handleSignIn(form: AuthRequest) {
    await SigninApi(form)
    .unwrap()
    .then((res)=>{
      dispatch(setCredentials(res))
      toast.success("Cadastrado com sucesso")  
      handleClose();    
    }).catch(()=>{})
  }


  return (
    <Modal
      open={open}
      onClose={handleClose}
    >
      <Box sx={style} component="form" onSubmit={handleSubmit(isLogIn ? handleLogin : handleSignIn)}>

        {isLogIn ?
          <Typography variant="h4" fontWeight={"bold"}>Entrar</Typography> :
          <Typography variant="h4" fontWeight={"bold"}>Cadastrar</Typography>
        }

        <TextField
          error={!!errors.username}
          helperText={errors.username?.message}
          {...register('username')}
          label="Username" sx={{ bgcolor: "white", fontWeight: 'bold', '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: 'white' } }}>
        </TextField>
        <TextField
          type="password"
          error={!!errors.password}
          helperText={errors.password?.message}
          {...register('password')}
          label="Password" sx={{ bgcolor: "white", fontWeight: 'bold', '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: 'white' } }}>
        </TextField>

        <Box sx={{ width: "100%" }}>
          <Grid2 container flexGrow={1} spacing={1} >
            <Grid2 size={"grow"} sx={{ display: "flex", alignItems: "center" }}>
              <Button type="submit" variant='contained' size='large' color="primary" sx={{ height: "100%", width: "100%" }}>{isLogIn ? "Entrar" : "Cadastrar"}</Button>
            </Grid2>
            <Grid2 size={"grow"} >
              <Button variant='contained' size='large' color='secondary' onClick={handleClose} sx={{ height: "100%", width: "100%" }}>Cancelar</Button>
            </Grid2>
          </Grid2>
        </Box>
      </Box>
    </Modal>
  )
}

export default ModalLogin
