import { Box, Typography } from '@mui/material'
import { forwardRef, useEffect, useImperativeHandle, useRef, useState } from 'react'
import { MicrowaveConfig } from '../Model/MicrowaveConfigModel'
import { MicrowaveState } from '../Model/MicrowaveStateModel'
import { MICROWAVE_CONFIG } from '../appConfig'

type Props = {
  state: MicrowaveState
  config: MicrowaveConfig | null
  onFinish: VoidFunction
}

export type MicrowaveDisplayMethods = {
  add30Seconds: () => void;
};

const MicrowaveDisplay = forwardRef<MicrowaveDisplayMethods, Props>(({ state, config, onFinish }, ref) => {
  const [remainingTime, setRemainingTime] = useState<number>(config ? config.time : 1);
  const timerRef = useRef<number | null>(null);
  const [str, setStr] = useState("")
  const lastState = useRef<MicrowaveState>("idle");

  function add30Seconds() {
    setRemainingTime(prev => prev + 30);
  };

  // Expor mtodos para o componente pai
  useImperativeHandle(ref, () => ({
    add30Seconds,
  }));

  function printString() {
    if (config) {
      const str = config.program ? config.program.str : MICROWAVE_CONFIG.DEFULAT_STR
      const dotsNum: string = str.repeat(config.power);
      setStr(prev => prev.concat("\n" + dotsNum))
    }
  }
  function formatTime(time: number) {
    const minutes = Math.floor(time / 60);
    const seconds = time % 60;
    return `${String(minutes).padStart(2, "0")}:${String(seconds).padStart(2, "0")}`;
  };



  function startTimer() {
    timerRef.current = window.setInterval(() => {
      
      setRemainingTime((prev) => {
        if (prev <= 1) {
          clearInterval(timerRef.current!);
          timerRef.current = null;
          onFinish()
          return 0;
        }       
        return prev - 1;
      });
      printString()
    }, 1000);

  }

  function pauseTimer() {
    if (timerRef.current !== null) {
      clearInterval(timerRef.current);
      timerRef.current = null;
    }
  }
  
  useEffect(() => {
    switch (state) {
      case "running":        
        if (config && timerRef.current === null) {
          if(lastState.current === "idle"){
            setStr(`Ptencia: ${config.power}`);
          }
          setRemainingTime((prev) => (prev === 0 ? config.time : prev));
          startTimer()
        }        
        break
      case "pause":
        
        pauseTimer()
        break
      case "idle":
        if(lastState.current === "running"){
          setStr(prev=>prev.concat(`\nAquecimento concludo`));
        }
        setRemainingTime(0)
        break
    }
    lastState.current = state
    // Limpeza do timer quando o componente desmontar ou state/config mudar
    return () => {
      if (timerRef.current !== null) {
        clearInterval(timerRef.current);
        timerRef.current = null;
      }
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [state, config]);


  /* useEffect(() => {
    console.group("State listener")
    console.log("State", state)
    console.log("Config", config)
    console.groupEnd()
  }, [state, config]) */

  return (
    <Box
      sx={{ bgcolor: "#FF7700", display: "flex", flexDirection: "column", height: "590px", borderRadius: 3, width: "100%" }}>
      <Box
        sx={{ bgcolor: "#FFC66B", flexGrow: 1, maxHeight: "100%", overflowY: "auto", padding: 1, pl: 2, borderRadius: 3 }}>
        <pre>{str}</pre>
      </Box>
      <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
        <Typography variant='h3' sx={{ ml: 2 }} fontWeight={"bold"}>{formatTime(remainingTime)}</Typography>
        <Typography variant='h4' sx={{ mr: 2 }} >{state === "running" ? "Esquentando..." : state === "pause" ? "Pausado" : "Desligado"}</Typography>
      </Box>
    </Box>
  )
})

export default MicrowaveDisplay