import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
import { ENDPOINT } from "../appConfig";
import { AuthRequest } from "../Model/DTO/AuthRequest";
import { handleQueryError } from "../Utils/ErrorHandler";
import { toast } from "react-toastify";
import { AuthResponse } from "../Model/DTO/AuthResponse";
import { RootState } from "../context/Store";


export const authApi = createApi({
  reducerPath: "authApi",
  baseQuery: fetchBaseQuery({ baseUrl: ENDPOINT + "/Auth", 
    prepareHeaders: (headers, { getState }) => {
      const token = (getState() as RootState).auth.token;
      if (token) {
        headers.set('Authorization', `Bearer ${token}`);
      }
      return headers;
    },
  }),
  
  endpoints: (builder) => ({
    login: builder.mutation<AuthResponse, AuthRequest>({
      query: (credentials) => ({
        url: 'login',
        method: 'POST',
        body: credentials,
      }),
      onQueryStarted: async (user, { dispatch, queryFulfilled }) => {
        try {
          const { data } = await queryFulfilled;
        } catch (error) {
          toast.error(handleQueryError(error)) ;
        }
      },
    }),
    signin: builder.mutation<AuthResponse, AuthRequest>({
      query: (credentials) => ({
        url: 'register',
        method: 'POST',
        body: credentials,
      }),
      onQueryStarted: async (user, { dispatch, queryFulfilled }) => {
        try {
          const { data } = await queryFulfilled;
        } catch (error) {
          toast.error(handleQueryError(error));
        }
      },
    }),
  }),
})

export const { useLoginMutation, useSigninMutation } = authApi;
