import { Box, Button, Card, Grid2 } from '@mui/material'
import { useRef, useState } from 'react'
import MicroaveControl from '../Components/MicrowaveControl'
import MicrowaveDisplay, { MicrowaveDisplayMethods } from '../Components/MicrowaveDisplay'
import { MicrowaveConfig } from '../Model/MicrowaveConfigModel'
import { MicrowaveState } from '../Model/MicrowaveStateModel'
import { toast } from 'react-toastify'
import ModalLogin from '../Components/ModalLogin'
import { useSelector } from 'react-redux'
import { RootState } from '../context/Store'
import UnouthorizedCard from '../Components/UnouthorizedCard'

type Props = {}

const MicroondasPage = (props: Props) => {
  const auth = useSelector((state: RootState) => state.auth)
  const [currentState, setCurrentState] = useState<MicrowaveState>("idle")
  const [currentConfig, setCurrentConfig] = useState<MicrowaveConfig | null>(null)
  const [openModal, setOpenModal] = useState(false)
  const displayRef = useRef<MicrowaveDisplayMethods>(null);
  const isLogin = useRef<boolean>(false);

  function onStartPress(config: MicrowaveConfig) {
    switch (currentState) {
      case "idle":
        setCurrentConfig(config)
        setCurrentState("running")
        break;
      case "running":
        if (!config.program)
          displayRef.current?.add30Seconds();
        else toast.error("No  possivel para programas pr-definidos")
        break;
      case "pause":
        setCurrentState("running")
        break;
    }
  }

  function onPausePress() {
    switch (currentState) {
      case "idle":
        setCurrentConfig(null)
        break;
      case "running":
        setCurrentState("pause")
        break;
      case "pause":
        setCurrentConfig(null);
        setCurrentState("idle")
        break;
    }
  }

  function onFinish() {
    setCurrentConfig(null)
    setCurrentState("idle")
  }
  function handleLogIn(): void {
    isLogin.current = true
    setOpenModal(true);
  }

  function handleSignIn(): void {
    isLogin.current = false
    setOpenModal(true);
  }

  return (
    <Box sx={{ display: "flex", width: "100vw", height: "100vh", justifyContent: "center", alignItems: "center", flexDirection: "column" }}>
     <Box sx={{ display: "flex", gap: 1, mb: 2}}>
      <Button variant='contained' onClick={()=>handleLogIn()}>Entrar</Button>
      <Button variant='contained'onClick={()=>handleSignIn()}>Cadastrar</Button>
      <ModalLogin open={openModal} handleClose={()=>setOpenModal(false)} isLogIn={isLogin.current}/>
      </Box>
      <Card
        sx={{ width: "60vw", height: "600px", backgroundColor: "#1a1c1f", p: 2, display: "flex", flexDirection: "column", borderRadius: 3 }}>
        <Grid2 container spacing={2} flexGrow={1}>
          <Grid2 size={8}>
            <MicrowaveDisplay ref={displayRef} state={currentState} config={currentConfig} onFinish={onFinish} />
          </Grid2>
          <Grid2 size={4} sx={{ display: "flex", flexDirection: "row" }}>
            {auth.username ?
            <MicroaveControl onPause={onPausePress} onStart={onStartPress} state={currentState} />:
            <UnouthorizedCard />
            }
          </Grid2>
        </Grid2>
      </Card>
    </Box>
  )
}

export default MicroondasPage