import { yupResolver } from "@hookform/resolvers/yup";
import { Box, Button, Grid2, Modal, SxProps, TextField, Theme, Typography } from '@mui/material';
import { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch } from "react-redux";
import { toast } from 'react-toastify';
import * as Yup from "yup";
import { AppDispatch } from "../context/Store";
import { addProgramConfig, updateProgramConfig } from "../context/ProgramConfigSlice";
import { ProgramConfig } from '../Model/ProgramConfigModel';
import { useAddProgramConfigApiMutation, useUpdateProgramConfigApiMutation } from "../services/ProgramService";
type Props = {
  open: boolean,
  handleClose: VoidFunction
  program: ProgramConfig | undefined
}
type ProgramConfigForm = Omit<ProgramConfig, "isDefault">;
const style: SxProps<Theme> = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  borderRadius: 3,
  boxShadow: 24,
  p: 4,
  display: "flex",
  flexDirection: "column",
  gap: 1
};

const validation = Yup.object().shape({
  id: Yup.number().optional(),
  name: Yup.string().required("Precisa de um nome"),
  food: Yup.string().required("Insira os alimentos"),
  time: Yup.number().min(1, "Tempo invalido").max(120, "Tempo invalido").required("Insira um tempo"),
  power: Yup.number().min(1, "Potncia invalido").max(10, "Potncia invalido").required("Insira uma potencia"),
  str: Yup.string().required("Insira uma string"),
  instructions: Yup.string().optional(),
});

const ModalProgram = ({ open, handleClose, program }: Props) => {
  const [addProgramConfigApi] = useAddProgramConfigApiMutation();
  const [updateProgramConfigApi] = useUpdateProgramConfigApiMutation();
  const dispatch = useDispatch<AppDispatch>();
  const { register, handleSubmit, formState: { errors }, reset, } = useForm<ProgramConfigForm>({ resolver: yupResolver(validation) });

  useEffect(() => {
    if (open) {
      reset(program ?? { name: "", food: "", time: 1, power: 10, str: "", instructions: "" });
    }
  }, [open, program, reset]);

  async function handleAddConfig(form: ProgramConfigForm) {
    await addProgramConfigApi({ ...form})
      .unwrap()
      .then((res) => {
        dispatch(addProgramConfig(res))
        toast.success("Sucesso");
        handleClose()
      })
      .catch(() => { })
  }

  async function handleUpdateConfig(form: ProgramConfigForm) {
    console.log(program?.id)
    if (program){
      const newProgram: ProgramConfig = { ...form, isDefault: false, id: program.id }
      await updateProgramConfigApi(newProgram)
        .unwrap()
        .then(() => {
          dispatch(updateProgramConfig(newProgram))
          toast.success("Sucesso");
          handleClose()
        })
        .catch(() => {
          console.log("Error")
        })
      }
  }
  return (
    <Modal
      open={open}
      onClose={handleClose}
    >
      <Box sx={style} component="form" onSubmit={handleSubmit(program ? handleUpdateConfig : handleAddConfig)}>
        <Typography variant="h4" fontWeight={"bold"}>
          Novo Programa
        </Typography>
        <TextField
          error={!!errors.name}
          helperText={errors.name?.message}
          {...register('name')}
          label="Nome do Programa" sx={{ bgcolor: "white", fontWeight: 'bold', '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: 'white' } }}>
        </TextField>
        <TextField
          error={!!errors.food}
          helperText={errors.food?.message}
          {...register('food')}
          label="Alimento" sx={{ bgcolor: "white", fontWeight: 'bold', '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: 'white' } }}>
        </TextField>
        <TextField
          type='number'
          error={!!errors.time}
          helperText={errors.time?.message}
          {...register('time')}
          label="Tempo em Segundos" sx={{ bgcolor: "white", fontWeight: 'bold', '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: 'white' } }}>
        </TextField>
        <TextField
          type='number'
          error={!!errors.power}
          helperText={errors.power?.message}
          {...register('power')}
          label="Potencia" sx={{ bgcolor: "white", fontWeight: 'bold', '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: 'white' } }}>
        </TextField>
        <TextField
          error={!!errors.str}
          helperText={errors.str?.message}
          {...register('str')}
          label="String" sx={{ bgcolor: "white", fontWeight: 'bold', '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: 'white' } }}>
        </TextField>
        <TextField
          error={!!errors.instructions}
          helperText={errors.instructions?.message}
          {...register('instructions')}
          label="Instrues de uso" sx={{ bgcolor: "white", fontWeight: 'bold', '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: 'white' } }}>
        </TextField>
        <Box sx={{ width: "100%" }}>
          <Grid2 container flexGrow={1} spacing={1} >
            <Grid2 size={"grow"} sx={{ display: "flex", alignItems: "center" }}>
              <Button type="submit" variant='contained' size='large' color="primary" sx={{ height: "100%", width: "100%" }}>Salvar</Button>
            </Grid2>
            <Grid2 size={"grow"} >
              <Button variant='contained' size='large' color='secondary' onClick={handleClose} sx={{ height: "100%", width: "100%" }}>Cancelar</Button>
            </Grid2>
          </Grid2>
        </Box>
      </Box>
    </Modal>
  )
}

export default ModalProgram
