import { Box, Typography } from '@mui/material'
import React from 'react'

type Props = {}

const UnouthorizedCard = (props: Props) => {
  return (
    <Box sx={{
      color: "white",
      display: "flex", flexDirection: "column",
      alignItems: "center",
      height: "100%", width: "100%",
      justifyContent: "Center"
    }}>
      <Typography variant='h4'>Sem Autorizao</Typography>
    </Box>
  )
}

export default UnouthorizedCard