// src/utils/errorHandler.ts
import { FetchBaseQueryError } from '@reduxjs/toolkit/query';

type FetchError = {
  data: { message: string }
  status: number
  originalStatus: string
}
export function handleQueryError(obj: any): string {
  if ('error' in obj) {
    
    let status = 'originalStatus' in obj.error ? (obj.error as FetchError).originalStatus :(obj.error as FetchError).status
    console.log(obj.error)
    switch (status) {
      case 401:
        return `${status}: Credenciais Invalidas.`;
      case 409:
        return `${status}: String j existe no servidor`;
      case 404:
        return `${status}: Recurso no encontrado.`;
      case 400:
        return `${status}: Requisio mal formatada.`;
      case 500:
        return `${status}: Erro interno no servidor.`;
      default:
        return `${status}: Erro desconhecido`;
    }
  } else {
    return "Erro desconhecido.";
  }
}
