import { Add, Delete, Edit, Pause, PlayArrow } from '@mui/icons-material';
import { Box, Button, FormControl, Grid2, IconButton, InputLabel, MenuItem, Select, SelectChangeEvent, TextField, Typography } from '@mui/material';
import { ChangeEvent, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from "react-toastify";
import { AppDispatch, RootState } from '../context/Store';
import { deleteProgramConfig, setProgramConfig } from '../context/ProgramConfigSlice';
import { MicrowaveConfig } from '../Model/MicrowaveConfigModel';
import { MicrowaveState } from "../Model/MicrowaveStateModel";
import { ProgramConfig } from "../Model/ProgramConfigModel";
import { useDeleteProgramConfigApiMutation, useGetProgramConfigsApiQuery } from '../services/ProgramService';
import ModalProgram from './ModalProgram';
type Props = {
  onStart: (config: MicrowaveConfig) => void
  onPause: VoidFunction
  state: MicrowaveState
}

const MicroaveControl = ({ onStart, onPause, state }: Props) => {
  const { data } = useGetProgramConfigsApiQuery();
  const [deleteProgramConfigApi] = useDeleteProgramConfigApiMutation();
  const dispatch = useDispatch<AppDispatch>();
  const programs = useSelector((state: RootState) => state.programConfig)
  const [openModal, setOpenModal] = useState(false)
  const [timeString, setTimeString] = useState("")
  const [time, setTime] = useState(0)
  const [power, setPower] = useState("")
  const [selectedProgram, setSelectedProgram] = useState<ProgramConfig>()
  const [programToEdit, setProgramToEdit] = useState<ProgramConfig>()

  useEffect(() => {
    if (data) {
      dispatch(setProgramConfig(data))
    }
  }, [data, dispatch]);


  function handleStart() {
    if (timeString === "" && power === "") {
      onStart({ time: 30, power: 10, program: undefined })
      return
    }

    let numTime = Number(time);
    let numPower = Number(power);
    console.log("numTime", numTime)
    console.log("numPower", numPower)
    if (!numTime) {
      toast.info(" preciso informar um tempo vlido");
      return;
    }
    if (power === "") {
      numPower = 10;
    }
    if (numPower < 1 || numPower > 10) {
      toast.info(" preciso informar uma potncia vlida");
      return;
    }

    onStart({ time: numTime, power: numPower, program: selectedProgram });
  }

  function handlePause() {
    switch (state) {
      case "idle":
      case "pause":
        setPower("");
        setTimeString("");
        setTime(0);
        setSelectedProgram(undefined)
        onPause()
        break;
      case "running":
        onPause()
        break
    }
  }

  function formatTime(value: string) {
    const num = Number(value) || 1;
    const minutes = Math.floor(num / 60);
    const seconds = num % 60;
    return `${String(minutes).padStart(2, "0")}:${String(seconds).padStart(2, "0")}`;
  }

  function handleTimeChange(event: ChangeEvent<HTMLInputElement>): void {
    setTimeString(event.target.value)
  }

  function handleTimeBlur(): void {
    if (timeString === "") {
      return
    }
    const num = Number(timeString)
    if (isNaN(num)) {
      setTimeString("");
      setTime(0)
      return
    }
    if (num <= 120 && num > 0) {
      setTimeString(formatTime(timeString))
      setTime(num)
    } else {
      setTimeString("")
      setTime(0)
      toast.info("Insira um tempo vlido")
    }
  }

  function handlePowerChange(event: ChangeEvent<HTMLInputElement>): void {
    setPower(event.target.value)
  }

  function handleProgramChange(event: SelectChangeEvent<string>): void {
    const programId = Number(event.target.value);
    const selected = programs.find(prog => prog.id === programId);
    setSelectedProgram(selected);
    if (selected) {
      setPower(selected.power.toString())
      setTimeString(formatTime(selected.time.toString()))
      setTime(selected.time)
    } else {
      setPower("")
      setTimeString("")
    }
  }

  function handleEdit(id: number | undefined): void {
    const selected = programs.find(prog => prog.id === id);
    setProgramToEdit(selected)
    setOpenModal(true)
  }

  function handleCloseModal() {
    setOpenModal(false);
    setProgramToEdit(undefined);
    setSelectedProgram(undefined);
    setPower("");
    setTimeString("");
  }
  function handleOpenModal() {
    setProgramToEdit(undefined)
    setOpenModal(true);
  }

  async function handleDelete(id: number | undefined) {
    if (id)
      await deleteProgramConfigApi(id)
        .unwrap().then(() => {
          toast.success("Programa deletado");
          dispatch(deleteProgramConfig(id))
          setSelectedProgram(undefined)
          setPower("")
          setTimeString("")
        }).catch(()=>{})
  }

  return (
    <Box sx={{
      color: "white",
      display: "flex", flexDirection: "column",
      alignItems: "center",
      height: "100%", width: "100%",
      justifyContent: "space-between"
    }}>
      <ModalProgram open={openModal} handleClose={() => handleCloseModal()} program={programToEdit} />
      <Box>
        <Typography variant='h5'>Configuraes</Typography>
        <TextField
          disabled={state !== "idle" || !!selectedProgram}
          value={timeString}
          fullWidth
          onChange={handleTimeChange}
          onBlur={handleTimeBlur}
          label="Tempo" sx={{ my: 1, bgcolor: "white", fontWeight: 'bold', '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: 'white' } }}
        ></TextField>
        <TextField
          disabled={state !== "idle" || !!selectedProgram}
          value={power}
          onChange={handlePowerChange}
          fullWidth
          label="Potncia" sx={{ mb: 1, bgcolor: "white", fontWeight: 'bold', '&.Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: 'white' } }}
        ></TextField>
        <Typography variant='h5'>Programas</Typography>
        <Box sx={{ width: "100%", mt: 1 }}>
          <Grid2 container flexGrow={1} spacing={2} >
            <Grid2 size={2} sx={{ display: "flex", alignItems: "center" }}>
              <Button variant='contained' sx={{ height: "100%" }} onClick={() => handleOpenModal()}><Add sx={{ fontSize: "2rem" }} /></Button>
            </Grid2>
            <Grid2 size={10} >
              <FormControl fullWidth>
                <InputLabel
                  sx={{ transition: 'none', '&.Mui-focused': { display: 'none' } }}>
                  Programaes
                </InputLabel>
                <Select
                  value={selectedProgram?.id?.toString() || ""}
                  onChange={handleProgramChange}
                  label="Programaes"
                  disabled={state !== "idle"}
                  sx={{ bgcolor: "white", '&.Mui-focused .MuiOutlinedInput-notchedOutline': { border: "none" } }}>

                  <MenuItem>Nenhum</MenuItem>
                  {programs.map(prog => (
                    <MenuItem key={prog.id} value={prog.id} style={{ fontStyle: prog.isDefault ? 'normal' : 'italic', display: 'flex', alignItems: 'center' }}>
                      {!prog.isDefault && (<><IconButton size="small" onClick={() => handleEdit(prog.id)} sx={{ p: 0 }}>
                        <Edit />
                      </IconButton>
                        <IconButton size="small" onClick={() => handleDelete(prog.id)} sx={{ p: 0, mr: 2 }}>
                          <Delete />
                        </IconButton></>)}
                      <div style={{ display: 'flex', flexDirection: 'column' }}>
                        <span>{prog.name}</span>
                        <span>
                          <strong>Tempo:</strong> {formatTime(prog.time.toString())}
                          <strong> Potncia:</strong> {prog.power}
                        </span>
                      </div>
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid2>
          </Grid2>
        </Box>
        {selectedProgram && (
          <Box sx={{ width: "100%", mt: 1 }}>
            <Typography><b>Comida:</b> {selectedProgram.food}</Typography>
            <Typography><b>Instrues: </b>{selectedProgram.instructions}</Typography>
          </Box>
        )}
      </Box>
      <Box sx={{ width: "100%" }}>
        <Grid2 container flexGrow={1} spacing={1} >
          <Grid2 size={"grow"} sx={{ display: "flex", alignItems: "center" }}>
            <Button variant='contained' size='large' color="success" onClick={() => handleStart()} sx={{ height: "100%", width: "100%" }}><PlayArrow />Iniciar</Button>
          </Grid2>
          <Grid2 size={"grow"} >
            <Button variant='contained' size='large' color='error' onClick={() => handlePause()} sx={{ height: "100%", width: "100%" }}><Pause />Pausar</Button>
          </Grid2>
        </Grid2>
      </Box>
    </Box>
  )
}

export default MicroaveControl
